import * as React from 'react';

// const useState = React.useState;
// const useEffect = React.useEffect;
import {Route, Redirect, HashRouter} from 'react-router-dom';

import {Home, Login, Register} from '.';
import {ConfigProvider} from 'antd';
import zhCN from 'antd/es/locale-provider/zh_CN'; //  antd 

export const Layout = () => {
    //  "count"  state 
    // const [zoom, setZoom] = useState('scale(0)');
    // Similar to componentDidMount and componentDidUpdate:
    // useEffect(() => {});

    return (
        <ConfigProvider locale={zhCN}>
            <div className='layout'>
                {/* exact  */}
                <HashRouter>
                    <Route
                        exact
                        path='/'
                        render={() => <Redirect to='/login'/>}
                    />
                    <Route exact path='/dashboard' component={Home} title=''/>
                    <Route exact path='/login' component={Login} title=''/>
                    <Route path='/register' component={Register} title=''/>
                </HashRouter>
            </div>
        </ConfigProvider>
    );
};
