import * as React from 'react';
import '../style/login.scss';
import { Form, Icon, Input, Button, Row, Col, Carousel } from 'antd';

import { Redirect } from 'react-router-dom';

const iconEle = (name: string) => (
    <Icon type={name} style={{ color: 'rgba(0,0,0,.25)' }} />
);

const FormEle = (props: any) => {
    const { getFieldDecorator } = props.form;

    const [code, setCode] = React.useState(updateCode());

    const rule = {
        username: {
            rules: [
                {
                    required: true,
                    message: '',
                },
            ],
        },
        password: {
            rules: [
                {
                    required: true,
                    message: '',
                },
            ],
        },
        code: {
            rules: [
                {
                    required: true,
                    message: '',
                },
            ],
        },
    };

    function updateCode(): object {
        return {
            backgroundImage: `url(http://101.200.41.205:8080/captcha.php?${+new Date()})`,
        };
    }

    function handleSubmit(e: React.FormEvent): void {
        e.preventDefault();
        props.form.validateFields((err: Error, values: Object) => {
            if (!err) console.log(': ', values);

            return <Redirect to='/home' />;
        });
    }

    return (
        <Form onSubmit={handleSubmit} className='login-form'>
            <Form.Item>
                {getFieldDecorator('username', rule.username)(
                    <Input
                        size='large'
                        prefix={iconEle('user')}
                        placeholder=''
                    />,
                )}
            </Form.Item>
            <Form.Item>
                {getFieldDecorator('password', rule.password)(
                    <Input
                        size='large'
                        prefix={iconEle('lock')}
                        type='password'
                        placeholder=''
                    />,
                )}
            </Form.Item>
            <Form.Item>
                <Row gutter={16}>
                    <Col span={15}>
                        {getFieldDecorator('code', rule.code)(
                            <Input
                                size='large'
                                placeholder=''
                                prefix={iconEle('key')}
                            />,
                        )}
                    </Col>
                    <Col span={9}>
                        {
                            <Button
                                size='large'
                                className='code'
                                type='link'
                                onClick={() => setCode(updateCode())}
                                style={code}></Button>
                        }
                    </Col>
                </Row>
            </Form.Item>
            <Form.Item>
                <Button
                    style={{ width: '100%' }}
                    type='primary'
                    size='large'
                    htmlType='submit'
                    className='login-form-button'>
                     
                </Button>
            </Form.Item>
        </Form>
    );
};

const CarouselEle = () => {
    const images = [
        {
            img: 'http://101.200.41.205:8080/images/redesign/1.png',
            title: '',
            dec:
                '',
        },

        {
            img: 'http://101.200.41.205:8080/images/redesign/2.png',
            title: '',
            dec:
                ',',
        },

        {
            img: 'http://101.200.41.205:8080/images/redesign/3.png',
            title: '',
            dec:
                ',,',
        },

        {
            img: 'http://101.200.41.205:8080/images/redesign/4.png',
            title: '',
            dec:
                ',',
        },
    ];
    const Items = images.map((item, index) => {
        return (
            <div className='carousel_item' key={index}>
                <div>
                    <img src={item.img} alt={item.title} />
                </div>
                <h3>{item.title}</h3>
                <p>{item.dec}</p>
            </div>
        );
    });
    return <Carousel>{Items}</Carousel>;
};
export const Login = () => {
    document.title = '';
    const Logo = require('../assets/logo_anbai.png');
    const LoginForm = Form.create({ name: 'normal_login' })(FormEle);
    return (
        <div className='login'>
            {/* Logo */}
            <div className='logo'>
                <img src={Logo} alt='Logo AnBai' />
            </div>
            {/*  */}
            <div className='content'>
                <Row gutter={30}>
                    <Col lg={9} md={24} sm={24}>
                        {/* <p className='head title'></p> */}
                        <div className='box'>
                            <div className='form-name'>
                                <span></span>
                            </div>
                            {/*  */}
                            <LoginForm />
                        </div>
                    </Col>
                    <Col lg={15} md={24} sm={24}>
                        <CarouselEle />
                    </Col>
                </Row>
            </div>
        </div>
    );
};
